<template>
  <div id="goodsPage"  class="goodsDetail">
    <!--顶部吸顶-->
    <!--<van-skeleton title :row="3" />-->
    <div v-show="show.normalTitle" class="title" :style="setTopHeight">
      <img class="arrow" :src='icon.arrowBg' @click="onClickLeft">
      <!-- <img class="cart" :src="icon.shoppingCartBg">-->
      <van-icon class="cart" :class="skuNum>=100?'cartMax':''" :name="icon.shoppingCartBg" :info="skuNum!=0?(skuNum<=99?skuNum+'':'99+'):''"
        @click="turnToCart"></van-icon>
      <img v-show="show.beforeCollect" class=collection :src="icon.collectionBg" @click="addCollection">
      <img v-show="show.afterCollect" class=collection :src="icon.afterCollectionBg" @click="addCollection">
    </div>
    <div v-show="show.activeTitle" class="actTitle" :style="setTopHeight">
      <img class="arrow" :src='icon.arrow' @click="onClickLeft">
      <!--<img class="cart" :src="icon.shoppingCart">-->
      <van-icon class="cart" :class="skuNum>=100?'cartMax':''" :name="icon.shoppingCart" :info="skuNum!=0?(skuNum<=99?skuNum+'':'99+'):''"
        @click="turnToCart"></van-icon>
      <img v-show="show.beforeCollect" class=afterCollection :src="icon.collection" @click="addCollection">
      <img v-show="show.afterCollect" class=afterCollection :src="icon.afterCollection" @click="addCollection">
      <div class="tabChange" v-if="lists.detailDescribe" :style="setTopHeight">
        <div class="tabTxtList">
      <div :class="{active:show.titShow==0}" class="evaluateBtn" @click="goodsChange">商品</div>
      <div :class="{active:show.titShow==1}" class="evaluateBtn" @click="titChange">评价</div>
      <div :class="{active:show.titShow==2}" class="detailBtn" @click="detailChange">详情</div>
        </div>
        <div :style="{'padding':'0px 3px'}" style='position: relative;'>
      <img :src="icon.topClick" class="leftImgBox" :class="show.titShow==1?'topClick':show.titShow==2?'choicePoint':''">
      <!-- <img :src="icon.topClick" class="choicePoint" v-if="show.titShow==2"> -->
      </div>
      </div>

      <div class="noDetailTabChange" v-if="!lists.detailDescribe" :style="setTopHeight">
        <div class="tabTxtList">
            <div :class="{active:show.titShow==0}" class="evaluateBtn" @click="goodsChange">商品</div>
            <div :class="{active:show.titShow==1}" class="evaluateBtn" @click="titChange">评价</div>
        </div>
        <div :style="{'padding':'0px 3px'}" style='position: relative;'>
      <img :src="icon.topClick"  class="leftImgBox1">
      <!-- <img :src="icon.topClick" class="choicePoint" v-if="show.titShow==2"> -->
      </div>
      </div>
      <!-- <img class="choicePoint" :src="icon.topClick" v-if="show.titShow==2"> -->
    </div>
    <div ref="goodsPageScroll" @scroll="handleScroll" class="mianScroll" :style="{'margin-bottom':bottomHeight+'px'}">
    <!--商品轮播图  lists.spuPicUrlList -->
    <van-swipe class="swipe" :loop="true" @change="bannerChange" :autoplay="3000">
      <van-swipe-item  v-for="(image,i) in productImgs" :key="i" >
        <img :src="image" :onerror="defaultImg" @click="bannerPreview(productImgs,i)"/>
        <div class="outSellBox" v-if="skuList.storeNumber==0">补货中</div>
      </van-swipe-item>

      <!-- <div class="van-swipe__indicators" >
        <i class="van-swipe__indicator van-swipe__indicator--active"></i>
      </div> -->
    </van-swipe>
     <!-- 网络异常显示骨架屏 -->
    <van-skeleton title :row="6" :style="{'margin-top':'20px'}" v-if="ifNetworkError"/>
    <div v-if="ifNetworkError==false">
    <!--商品价格和库存-->
<!--    <div class="price" >-->
<!--      <span class="priceTag" v-show="listLoadingBol">￥</span>-->
<!--      <span class="salePrice">{{skuList.skuPrice}}</span>-->
<!--      <span class="originalPrice" v-if="lists.priceLine!=''&&listLoadingBol">￥{{lists.priceLine}}</span>-->
<!--      <span class="stock"-->
<!--        v-show="skuList.storeNumber!=0&&skuList.storeNumber>lists.threshold">库存{{skuList.storeNumber}}件</span>-->
<!--      <span class="lowStock"-->
<!--        v-show="skuList.storeNumber!=0&&skuList.storeNumber<=lists.threshold">仅剩{{skuList.storeNumber}}件</span>-->
<!--    </div>-->
    <div class="price">
        <div class="priceBox">
            <div class="priceTag" v-show="listLoadingBol">￥</div>
            <div class="salePrice">{{skuList.skuPrice}}</div>
            <div class="originalPrice" v-if="lists.priceLine!=''&&listLoadingBol">￥{{lists.priceLine}}</div>
        </div>
        <div class="stockBox">
            <div class="stock"
                 v-show="skuList.storeNumber!=0&&skuList.storeNumber>lists.threshold">库存{{skuList.storeNumber}}件</div>
            <div class="lowStock"
                 v-show="skuList.storeNumber!=0&&skuList.storeNumber<=lists.threshold">仅剩{{skuList.storeNumber}}件</div>
        </div>
    </div>
    <!--商品名称\简介和分享-->
    <div class="goods">
      <div class="goodsDetailBox">
        <div >
          <div class="spuName">{{lists.spuName}}</div>
          <div class="salePoint">{{lists.spuSalePoint}}</div>
        </div>
        <div class="share">
          <img class="shareImg" :src="icon.share" @click="wechartShare" />
          <div>分享</div>
        </div>
      </div>
      <div class="goodsAttribute">
          <div class="goodsAttributeBox" :style="{'border':'0.5px solid #FB5768'}" v-if="lists.goodsBrand">品牌：{{lists.goodsBrand}}</div>
          <div class="goodsAttributeBox" :style="{'border':'0.5px solid #FB5768'}" v-if="lists.prodArea">产地：{{lists.prodArea}}</div>
      </div>
    </div>

    <!--商品标签.规格.领券.购买数量-->
    <div class="tag" ref="productTag" >
      <div class="oneTag" v-show="lists.cutOffTimeStr">
        <div class="redPoint"></div>
        <div class="tagInfo">{{lists.cutOffTimeStr}}</div>
      </div>
      <div class="oneTag" v-show="lists.labelList.length!=0" v-for="(tagInfoItem,index) in lists.labelList">
        <div class="redPoint"></div>
        <div class="tagInfo">{{tagInfoItem}}</div>
      </div>
    </div>
    <div class="spec" ref="changeTop">
      <div class="specTit">规格</div>
      <div class="specName">{{skuList.attrValueContent}}</div>
    </div>

<!--    <div class="couponTitBox" v-show="getPickupList.length>0" @click="couponList">-->
<!--      <div>-->
<!--        <div class="couponTit">领券</div>-->
<!--        <div class="goodscoupon">-->
<!--          <div class="couponTitleBox" v-for="(couponItem,index) in getPickupList" v-if="index<3" >{{couponItem.name.replace(/元/g,'')}}</div>-->
<!--        </div>-->
<!--      </div>-->
<!--      <img class="goodscouponMore" :src="icon.evaluate" />-->
<!--    </div>-->
    <div class="borderLine"></div>
    <!--购买数量-->
    <div class="shoppingNum" ref="shoppingNum">
      <div>购买数量</div>
      <div>
      <div class="stepperBox">
          <img class="subtractBtn" src="../../assets/images/mall/mall_icon_subtract.png" alt="">
          <img class="addBtn" src="../../assets/images/mall/icon_add_cut.png" alt="">
          <van-stepper class="stepper" v-model="goodsNum" integer
                       :max="skuList.storeNumber>999?999:skuList.storeNumber" :disable-minus="goodsNum==1?true:false" @overlimit='addNumBtn(skuList.storeNumber)' @change="getCount(skuList.storeNumber)"></van-stepper>
      </div>
    </div>
    </div>
        <div class="borderBox" v-show="fullReductionList.length>0||packList.length>0||getPickupList.length>0 || unionPayActList.length>0"></div>
        <div class="activityBox" v-if="fullReductionList.length>0||packList.length>0 || unionPayActList.length>0">
            <div class="activityTitle">活动</div>
            <div class="activityCon">
                <div class="fullReductionBox" v-if="fullReductionList.length>0" @click="goToreducedPackaging(0,fullReductionList[0].actCode)">
                    <div class="fullList">
                        <div class="fullIcon">满减</div>
                        <div class="fullItem">{{fullReductionList[0].actRule}}</div>
                    </div>
                    <div class="gotoList">
                        <div class="goToIcon">去专区</div>
                        <img class="goodscouponMore" :src="icon.evaluate" />
                    </div>
                </div>
                <div class="fullReductionBox" v-if="packList.length>0" @click="goToreducedPackaging(1,packList[0].actCode)">
                    <div class="fullList">
                        <div class="packIcon fullIcon">打包</div>
                        <div class="fullItem">{{packList[0].actRule}}</div>
                    </div>
                    <div class="gotoList">
                        <div class="goToIcon">去专区</div>
                        <img class="goodscouponMore" :src="icon.evaluate" />
                    </div>
                </div>
                <div class="fullReductionBox" v-for="(activity,index) in unionPayActList" :key="index" @click="goToUnionPayActivity(0,activity.id)">
                    <div class="fullList">
                        <img :src="activity.detailPic" class="unionPayActIcon">
                        <div class="fullItem">{{activity.detailLabel}}</div>
                    </div>
                    <div class="gotoList">
                        <div class="goToIcon">去专区</div>
                        <img class="goodscouponMore" :src="icon.evaluate" />
                    </div>
                </div>
            </div>
        </div>
        <div class="couponTitBox" v-show="getPickupList.length>0" @click="couponList">
            <div>
                <div class="couponTit">领券</div>
                <div class="goodscoupon">
                    <div class="couponTitleBox" v-for="(couponItem,index) in getPickupList" :key="index" v-if="index<3" >{{couponItem.name.replace(/元/g,'')}}</div>
                </div>
            </div>
            <img class="goodscouponMore" :src="icon.evaluate" />
        </div>
    <div class="borderBox"></div>
    <div class="freightBox">
      <div class="freightTxt">运费</div>
      <div class="freightInfo">
        <div>本店铺满{{shopInfo.orderBaseLine}}元起送，配送费{{shopInfo.baseLineFee}}元起 <span v-show="shopInfo.deliveryBaseLine!=0&&shopInfo.deliveryBaseLine!=null">，满{{shopInfo.deliveryBaseLine}}包邮</span>    </div>
<!--        <div v-show="shopInfo.deliveryBaseLine!=0">，满{{shopInfo.deliveryBaseLine}}包邮</div>-->
      </div>
    </div>
    <div class="borderBox"></div>
    <!-- 店铺信息 -->
    <div class="shopInfoBox" @click="toShop">
      <div class="shopInfo">
        <div><img class="shopPic" :src="shopInfo.shopPicUrl==null?ownerPicUrl:shopInfo.shopPicUrl" /></div>
        <div>
          <div class="shopName">{{shopInfo.shopName}}</div>
          <div class="goodsSaleNum">在售商品{{shopInfo.saleCount}}件</div>
        </div>
      </div>
      <div class="toShop">进店逛逛 &nbsp;<img :src="icon.evaluate"/></div>
    </div>
    <div class="borderBox"></div>
    <!--商品评价-->
    <div class="evaluate" ref="titChange">
      <div class="evaluateTit">商品评价</div>
      <div class="evaluateCli" @click="showMoreEval()">
        <span>查看所有评价</span>
        <img :src="icon.evaluate" />
      </div>
      <!--<span>{{evalContent}}</span>-->
    </div>
    <!--显示第一条评价-->
    <div v-if="show.firstEvaluate" class="evaContent">
      <div :style="{'display': 'flex','align-items': 'center','justify-content': 'space-between'}">
        <div :style="{'display': 'flex','align-items': 'center'}">
          <img class="evalOwnerPic" :src="(!evalInfo.ownerPicUrl || evalInfo.ownerPicUrl==null)?ownerPicUrl:evalInfo.ownerPicUrl" />
          <!--如果用户头像为空则显示默认头像-->
          <div class="ownerName">{{evalInfo.ownerNickname}}</div>
        </div>
        <van-rate class="evaluateRate" v-model="evalInfo.evalLevel" size='0.37rem' allow-half gutter=3 :icon="icon.like"
          :void-icon="icon.noLike" void-color="#eee" readonly />
      </div>
      <div class="evaTxt">{{evalInfo.evalContent}}</div>
      <div v-for="(item, index) in evalInfo.evalPicUrl">
        <img class="evalPic" v-lazy="item.picUrl" @click="preview(evalInfo.evalPicUrl,index)"/>
      </div>
    </div>
    <!--商品详情-->
    <div class="noDescribe" v-if="!lists.detailDescribe" ref="detailChange"></div>
    <div class="describe" v-if="lists.detailDescribe"  ref="detailChange">
      <div class="describeTxt">-商品详情-</div>
      <div class="describeDetail" v-html="lists.detailDescribe"></div>
    </div>
    <div ref="placeHolder" style="height:600px" v-show="isShowPlaceHolder"></div>
    </div>
    </div>
    <van-goods-action :style="{paddingBottom:this.$footBarHeight+'px'}">
      <van-goods-action-icon :icon="icon.shop" text="商家" @click="toShop" />
      <van-goods-action-icon :icon="icon.callShop" text="联系商家" @click="callShop" />
      <van-goods-action-button :class="{addCartBtn:skuList.storeNumber==0}" class="addCartBtn1" type="danger"
        text="加入购物车" @click="addToChart()" />
      <van-goods-action-button :class="{submitOrderBtn:skuList.storeNumber==0}" class="submitOrderBtn1" type="danger"
        text="立即购买" @click="goToBuy()" />
    </van-goods-action>
    <!--点击分享弹窗-->
    <van-action-sheet v-model="show.weChartShare" cancel-text="取消" @cancel="onCancel" round safe-area-inset-bottom>
      <p class="shareTit">分享至</p>
      <div class="share-box" :style="{'display':'flex','justify-content':'center'}">
        <div class="shareChart" @click="checkStoragePermissions(1)">
          <img :src="icon.shareChart" />
          <p>微信好友</p>
        </div>
        <div class="shareFriends">
          <img :src="icon.shareFriends" @click="checkStoragePermissions(2)" />
          <p>朋友圈</p>
        </div>
      </div>
      <div class="borderLine"></div>
    </van-action-sheet>
        <!--点击优惠券弹窗-->
    <van-action-sheet :style="{'overflow-x': 'hidden'}" v-model="show.couponList"  @cancel="onCancel" round safe-area-inset-bottom>
      <div class="couponHead">
          <div class="commonHead" @click="hideCoverUp()"><img src="@/assets/images/common/left_black.png">
          </div>
          <div class="title">优惠券</div>
      </div>
      <div class="couponList" v-if="getPickupList.length!=0">
                <div class="couponItem" v-for="(getPickupItem,i) in getPickupList" :key="'getPickupItem'+i">
                    <div class="couponInfo"
                        :class="(getPickupItem.isFinished==1&&getPickupItem.isPicked!=1)?'grayCouponInfo':(getPickupItem.isPicked==1?'carCouponInfo':'')">
                        <div class="couponBox">
                            <div class="couponFirst">
                                <div class="couponPriceBox">
                                    <div class="couponPriceNum">
                                        <span>￥</span>
                                        <span>{{getPickupItem.couponTotal}}</span>
                                    </div>
                                    <div class="rangeBox" @click="changeSeeRange(getPickupItem)">
                                        <div>适用范围</div>
                                        <img src="@/assets/images/mall/mall_arrow_down.png" alt=""
                                            v-if="!getPickupItem.seeRange">
                                        <img src="@/assets/images/mall/mall_arrow_up.png" alt=""
                                            v-if="getPickupItem.seeRange">
                                    </div>
                                </div>
                                <div class="couponRight">
                                    <div class="couponPriceTxt">
                                        <div>{{getPickupItem.shopName}}</div>
                                        <div>{{getPickupItem.name}}</div>
                                    </div>
                                    <div class="couponBottom">
                                        <div class="timeTxt" v-if="getPickupItem.effType == '1'">
                                            {{getPickupItem.effBeginDay}}-{{getPickupItem.effEndDay}}
                                        </div>
                                        <div class="timeTxt" v-if="getPickupItem.effType == '2'&&getPickupItem.effDays == 1">
                                            领取后当天有效
                                        </div>
                                        <div class="timeTxt" v-if="getPickupItem.effType == '2'&&getPickupItem.effDays != 1">
                                            领取后{{getPickupItem.effDays}}天内有效
                                        </div>
                                        <div class="couponBtn"
                                            v-if="getPickupItem.isPicked==0&&getPickupItem.isFinished==0">
                                            <div class="receiveBtn" @click="receiveBtn(getPickupItem)">立即领取</div>
                                        </div>
                                        <div class="couponBtn" v-if="getPickupItem.isPicked==1">
                                            <div class="receiveBtn" >已领取</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                    <div class="couponContent" v-show="getPickupItem.seeRange">
                        全品类通用
                    </div>
                </div>
            </div>
            <div class="noCouponList" v-if="getPickupList.length==0">
                <img src="@/assets/images/default/no_coupon_img.png" alt="">
                <div>暂无优惠券</div>
            </div>
    </van-action-sheet>


    <div class="topBtn" v-show="isShowTopBtn" @click="goTopBtn">
      <img src="@/assets/images/mall/top.png" alt="">
    </div>
    <!-- 放大预览图片 -->
    <Preview :images="images" ref="myPreview" v-if="isShowPreview"></Preview>
  </div>

</template>

<script>
    import {goodsDetail} from './goodsDetail'
    export default {
        name: 'goodsDetail',
        mixins:[goodsDetail]
    }
</script>
<style scoped="scoped" lang="scss">
    .freightBox{
      height: 50px;
      padding: 0 15px;
      display: flex;
      align-items: center;
      .freightTxt{
        white-space: nowrap;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #9A9A9A;
        letter-spacing: 0;
      }
      .freightInfo{
        display: flex;
        margin-left: 15px;
        div{
          font-family: PingFangSC-Regular;
          font-size: 13px;
          color: #333333;
          letter-spacing: 0;
        }
      }
    }
    .stepperBox{
        position: relative;
        .subtractBtn{
            width: 22px;
            height: 22px;
            position: absolute;
            top: 12px;
            left: 0;
        }
        .addBtn{
            width: 22px;
            height: 22px;
            position: absolute;
            top: 12px;
            right: 0;
        }
        /deep/.van-stepper__minus{
            opacity: 0;
        }
        /deep/.van-stepper__plus {
            opacity: 0;
        }
    }
    .activityBox{
        display: flex;
        justify-content: space-between;
        padding:15px;
        .activityTitle{
            font-family: PingFangSC-Regular;
            font-size: 14px;
            color: #9A9A9A;
            letter-spacing: 0;
        }
        .activityCon{
            width: 85%;
            .fullReductionBox{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 15px;
                .fullList{
                    display: flex;
                    align-items: center;
                    .fullIcon{
                        width: 35px;
                        height: 16px;
                        text-align: center;
                        line-height: 16px;
                        font-family: PingFangSC-Regular;
                        font-size: 10px;
                        color: #FFFFFF;
                        letter-spacing: 2px;
                        background-image: linear-gradient(143deg, #D8FF8A 0%, #66B22B 82%);
                        border-radius: 8px 0px 8px 0px;
                    }
                    .unionPayActIcon{
                      width: 35px;
                      height: 16px;
                    }
                    .packIcon{
                        background-image: linear-gradient(143deg, #FFA78E 0%, #FA452D 82%);
                    }
                    .fullItem{
                        font-family: PingFangSC-Regular;
                        font-size: 12px;
                        color: #888888;
                        letter-spacing: 0;
                        margin-left: 5px;
                    }
                }
                .gotoList{
                    display: flex;
                    align-items: center;
                    .goToIcon{
                        font-family: PingFangSC-Regular;
                        font-size: 12px;
                        color: #4A4A4A;
                        letter-spacing: 0;
                    }
                }
            }
            .fullReductionBox:last-child{
                margin-bottom: 0;
            }
        }
    }
  .topBtn{
    width: 50px;
    height: 50px;
    position: fixed;
    bottom: 80px;
    right: 20px;
    z-index: 10;
    img{
      width: 100%;
    }
  }
  #goodsPage {
    width: 100%;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
  }
.mianScroll{
  height: 100%;
  overflow-y: auto;
  overflow-x:hidden;
  background: #FFFFFF;
  /* margin-bottom: 55px; */
}
  .title {
    width: 100%;
    height: 48px;
    position: fixed;
    z-index: 10;
    top: 0;
    /*padding: 0 18px;*/
  }

  .actTitle {
    width: 100%;
    height: 48px;
    position: fixed;
    z-index: 10;
    top: 0px;
    /*padding: 0 18px;*/
    background: #FFFFFF;
  }

  .swipe {
  // height: 375px;
  // width: 100%;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  object-fit: cover;
  height: 375px!important;
  width: 375px!important;
  }
.picBackground{
  background-repeat: no-repeat;
  background-size: 100% 100%;
  object-fit: cover;
  height: 375px!important;
  width: 375px!important;
}
  .van-swipe img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    /*background-color: #42b983;*/
  }
  .arrow {
    width: 30px;
    padding: 10px 10px 3px 15px;
  }

  .collection {
    width: 30px;
    /*padding:27px 10px 3px 0px;*/
    padding-top: 10px;
    padding-right: 10px;
    float: right;
  }
  .afterCollection{
    width: 30px;
    /*padding:27px 10px 3px 0px;*/
    padding-top: 10px;
    padding-right: 10px;
    float: right;
  }
  .cart {
    width: 25px;
    /*padding: 27px 18px 3px 0px;*/
    float: right;
    padding-top: 10px;
    padding-right: 20px;
  }

  .cart /deep/img {
    display: block;
    /*width: 100%;*/
    height: 30px;
    width: 30px;
  }

  .van-info {
      font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
    top: 15px;
      right: 17px;
      width: 16px !important;
      height: 16px !important;
      text-align: center;
      border-radius: 0.21333rem;
      letter-spacing: -0.02px;
      background: #FC0238;
      position: absolute;
      font-size: 0.26667rem;
      color: #FFFFFF;
      box-sizing: border-box;
      display:flex;
